.App {
  text-align: center;
  font-family: 'Nanum Gothic'
}
.modal {
  background: linear-gradient(to right, #ff7e5f,#feb47b);
  border-radius: 15px;
  padding: 20px;
  max-width: 600px;
  margin: 20px auto;
}

.modal h1 {
color: #fff;
text-shadow: 0 0 4px rgba(0,0,0,0.3);
font-size: 28px;
}

.modal .content {
  font-size: 18px;
  font-weight: bold;
}

.modal .button {
  color: #fff;
  border: none;
  width: 200px;
  height: 45px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 20px 0;
  cursor: pointer;
  background-color: #f55e5e;
  border-radius: 50px;
}

.modal .button:hover {
  background-color: #e44848;
}

.modal-backdrop {
  padding: 20px;
  border-radius: 10px;
  background-color: #fff;
}

.modal-backdrop p+p {
  font-size: 22px;
  color: red;
  font-weight: bold;
}

@media (max-width:760px) {
  .modal {
    max-width: 80%;
  }
}